import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Camera, CameraOptions } from '@ionic-native/camera';

/*
  Generated class for the PictProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class PictProvider {

  myPhoto : any;

  constructor(public http: HttpClient,private camera: Camera) {
    console.log('Hello PictProvider Provider');
  }

  openCamera()
  {
    return new Promise((resolve,reject)=>{
      const options: CameraOptions = {
        quality: 70,
        destinationType: this.camera.DestinationType.DATA_URL,
        encodingType: this.camera.EncodingType.JPEG,
        mediaType: this.camera.MediaType.PICTURE
      }
    
      this.camera.getPicture(options).then((imageData) => {
      // imageData is either a base64 encoded string or a file URI
      // If it's base64:
      this.myPhoto = 'data:image/jpeg;base64,' + imageData;
      resolve(true)
      }, (err) => {
      // Handle error
      console.log('error, couldnt take the Image. ');
      reject(true);
      });  
    })
  }

  openGallery()
  {
    return new Promise((resolve,reject)=>{
    const options: CameraOptions = {
      quality: 70,
      destinationType: this.camera.DestinationType.DATA_URL,
      sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
      saveToPhotoAlbum:false
    }
  
    this.camera.getPicture(options).then((imageData) => {
    // imageData is either a base64 encoded string or a file URI
    // If it's base64:
    this.myPhoto = 'data:image/jpeg;base64,' + imageData;
    resolve(true)
    }, (err) => {
    // Handle error
    console.log('error, couldnt take the Image. ');
    reject(true);
    });  
  })
}

  cropPicture()
  {
    return new Promise((resolve,reject)=>{
    const options: CameraOptions = {
      quality: 70,
      destinationType: this.camera.DestinationType.DATA_URL,
      sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
      saveToPhotoAlbum:false,
      allowEdit:true,
      targetWidth:300,
      targetHeight:300
    }
  
    this.camera.getPicture(options).then((imageData) => {
    // imageData is either a base64 encoded string or a file URI
    // If it's base64:
    this.myPhoto = 'data:image/jpeg;base64,' + imageData;
    resolve(true)
    }, (err) => {
    // Handle error
    console.log('error, couldnt take the Image. ');
    reject(true);
    });  
  })
  }
}



